<template>
  <Side>
    <div class="skill-content">
      <div class="skill-in-content">
        <span class="skill-title">주요 기술</span>
      </div>
      <div class="skill-in-content">
        <span class="skill-title">관심 분야</span>
      </div>
    </div>
  </Side>
</template>
<script>
import Side from '../components/side.vue'
export default {
  name: 'skill-v',
  components: { Side },
  mounted() {
    let sideList = document.querySelectorAll('.side-m-item');
    sideList.forEach(element => {
      if(element.id != 'side-skill') {
        element.classList.remove('side-activity');
        element.classList.add('side-unactivity');
      } else {
        element.classList.remove('side-unactivity');
        element.classList.add('side-activity');
      }
    });
  },
}
</script>
<style>
.skill-content {
  padding: 100px 30px 30px 180px;
  color: white;
  cursor: default;
}
.skill-in-content { width: 100%; height: 35vh; }
.skill-title { font-size: 35px; }
</style>