<template>
  <Side>
    <div class="about-content">
      <h2>Android Developer</h2>
      <h1>Hi. I'm Geunyeol Kim</h1>
      <div class="path-content">
        <div class="path-box">
          <img src="../assets/img/github.svg">
          <span class="path-text" style="margin-left: 10px;">
            github.com/wpfhorez4
          </span>
        </div>
        <div class="path-box">
          <img src="../assets/img/gmail.svg">
          <span class="path-text" style="margin-left: 10px;">
            dev.geunyeol.kim@gmail.com
          </span>
        </div>
        <div class="path-box">
          <img src="../assets/img/smartphone.svg">
          <span class="path-text" style="margin-left: 10px;">
            010-2936-6707
          </span>
        </div>
      </div>
    </div>
  </Side>
</template>
<script>
import Side from '../components/side.vue'
export default {
  name: 'about-v',
  components: { Side },
  mounted() {
    let sideList = document.querySelectorAll('.side-m-item');
    sideList.forEach(element => {
      if(element.id != 'side-about') {
        element.classList.remove('side-activity');
        element.classList.add('side-unactivity');
      } else {
        element.classList.remove('side-unactivity');
        element.classList.add('side-activity');
      }
    });

    let paths = document.querySelectorAll('.path-text');
    var toastTimer;
    paths.forEach(element => {
      element.addEventListener('click', (e)=> {
        const target = e.target.innerHTML;
        window.navigator.clipboard.writeText(target);
        let toast = document.getElementById('toast');
        toast.classList.add('is-active');
        if(toastTimer != null) clearTimeout(toastTimer);
        toastTimer = setTimeout(()=>{
          toast.classList.remove('is-active');
          toastTimer = null;
        }, 2000)
      });
    });
  },
}
</script>
<style>
.about-content {
  padding: 100px 30px 30px 180px;
  color: white;
  cursor: default;
}
.about-content h1 {
  font-size: 50px;
  opacity: 0;
  animation: a-fadein 1s 0.7s forwards;
  -moz-animation: a-fadein 1s 0.7s forwards; /* Firefox */
  -webkit-animation: a-fadein 1s 0.7s forwards; /* Safari and Chrome */
  -o-animation: a-fadein 1s 0.7s forwards; /* Opera */
}
.about-content h2 {
  color: #25C565;
  font-size: 35px;
  opacity: 0;
  animation: a-fadein 1s 0.4s forwards;
  -moz-animation: a-fadein 1s 0.4s forwards; /* Firefox */
  -webkit-animation: a-fadein 1s 0.4s forwards; /* Safari and Chrome */
  -o-animation: a-fadein 1s 0.4s forwards; /* Opera */
}
.path-content {
  opacity: 0;
  margin-top: 80px;
  animation: a-fadein 1s 1.0s forwards;
  -moz-animation: a-fadein 1s 1.0s forwards; /* Firefox */
  -webkit-animation: a-fadein 1s 1.0s forwards; /* Safari and Chrome */
  -o-animation: a-fadein 1s 1.0s forwards; /* Opera */
}
.path-content img {
  width: 20px; height: 20px;
  filter: invert(100%) sepia(99%) saturate(2%) hue-rotate(178deg) brightness(111%) contrast(100%);
}
.path-box {
  display : flex;
  justify-content : left;
  align-items : center;
  padding: 10px 0px;
}
/*왼쪽에서 오른쪽으로 이동하는 에니메이션*/
.path-text{ position: relative; cursor: pointer; }
.path-text:after{
  content: ""; position: absolute;
  left: 0; bottom: -5px;
  width: 0px; height: 2px;
  margin: 5px 0 0;
  transition: all 0.2s ease-in-out;
  transition-duration: 0.3s;
  background-color: #009BEF;
  opacity: 0;
}
.path-text:hover:after{ width: 100%; opacity: 1; }
@keyframes a-fadein {
  0% { opacity: 0; transform: translate3d(0, -100%, 0); }
  to { opacity: 1; transform: translateZ(0); }
}
@-moz-keyframes a-fadein {
  0% { opacity: 0; transform: translate3d(0, -100%, 0); }
  to { opacity: 1; transform: translateZ(0); }
}
@-webkit-keyframes a-fadein {
  0% { opacity: 0; transform: translate3d(0, -100%, 0); }
  to { opacity: 1; transform: translateZ(0); }
}
@-o-keyframes a-fadein {
  0% { opacity: 0; transform: translate3d(0, -100%, 0); }
  to { opacity: 1; transform: translateZ(0); }
}
</style>